

.Landingpage {
    position:fixed;
    padding:0;
    margin:0;
    overflow-y: scroll;
    display:grid;
    grid-template-rows: 1000px 1000px;
    top:0;
    left:0;
    background:#fff;
    width: 100%;
    height: 100%;
    overflow-x:hidden;
}

.MobileFrontLogo {
    display: none;
}
.MobileFrontGraphic {
    display: none;
}

.TinyFrontGraphic {
    display:none;
}

.TinyFrontLogo {
    display:none;
}

.overallcontainer {
    position:relative;
    display: grid;
    grid-template: repeat(1, 1fr);
    grid-auto-rows: 40px;
    font-size: 1.2rem;
    background:#202020;
    height: 95%;
}


.Frontlogo {
    display: flex;
    align-items: center;
    justify-content: center;
    grid-row: 12;
    -webkit-transform: translate(-25%, -25%);
    transform: translate(-25%, 25%);
}


.FrontGraphic {
    display: flex;
    align-items: center;
    justify-content: center;
    grid-row: 10;
    -webkit-transform: translate(20%, 10%);
    transform: translate(20%, 10%);
}


.Landingpage-subcap {
    color: #fff;
    display: flex;
    justify-self: left;
    align-self: left;
    text-align: center;
    grid-row: 23;
    font-size: 1rem;
    margin-left: 18rem;
    width: 500px
}

.regcontainer {
    display: flex;
    align-items: center;
    justify-content: center;
    grid-row: 18;
    -webkit-transform: translate(-25%);
    transform: translate(-25%);
}


.btn_register {
    display: flex;
    background-color: #317DEE;
    color: #fff;
    float: left;
    padding: 25px 80px;
    border-radius: 50px;
}

.btn_register:hover {
    padding: 22px 77px;
    border:3px solid #317DEE;
    transition: all 0.25s ease-out;
    background: #fff;
    color: #317DEE;
}

.arrow_container {
    padding: 8px 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    grid-row: 20;
}

.front_arrow {
    cursor: pointer;
}

.white_container {
    position:relative;
    padding:0;
    margin-top: 126.445px;
    overflow-y:hidden;
    overflow-x:hidden;
    top:100px;
    left:0;
    background:#fff;
    width: 100%;
    height: 100%;
    display:grid;
    grid-template-rows: repeat(12, 1fr);
    grid-template-columns: 4fr 1fr; 
    grid-gap: 5px;
    grid-auto-rows: 10px;
    font-size: 1.1rem;
}

.white_triangle {
    display:flex;
    width: 0;
    height: 0;
    border-bottom: 150px solid #fff;
    border-left: 100vw solid transparent;
    background: #202020;
    grid-row:21;
}

.What_is_idc {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 52.2609px;
    line-height: 63px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #202020;
    grid-row: 7;
    width: 600px;
    -webkit-transform: translate(32%, 175%);
    transform: translate(32%, 175%);
    grid-column: 1;
}

.What_is_idc_section {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 34.8406px;
    line-height: 42px;
    color: #000C1D;
    display: flex;
    align-items: center;
    justify-content: center;
    grid-row: 8;
    width: 600px;
    -webkit-transform: translate(32%, 125%);
    transform: translate(32%, 125%);
}

.idc_pencil {
    grid-row: 9;
    display: flex;
    align-items: center;
    justify-content: center;
    -webkit-transform: translate(-45%, -30%);
    transform:translate(-45%, -30%);
    margin-left: 100px;
    width: 300px;
}

.icon_collection {
    grid-row: 12;
    grid-column: 2;
    display: flex;
    align-items: center;
    justify-content: center;
    -webkit-transform: translate(18%, -93%);
    transform:translate(18%, -93%);
}



